
li:hover{
  background-color: var(--robin-blue-color);
  border-radius: 10px;
}
.navbar{
  padding: 2.8rem 0;
}
.navbar__brand{
  width: 150px;
}
.brand__and__toggler{
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.navbar__collapse{
  display: none;
}
.navbar__collapse .nav__link{
  letter-spacing: 1px;
  transition: var(--transition);
  padding: 10px 12px;
}
.navbar__collapse .nav__link:hover, .nav__link{
  color: var(--black-color);
}


/* smallscreen */
.navbar__smallscreen{
  position: fixed;
  right: 0;
  top: 0;
  background-color: #30d6d6;;
  height: 100%;
  width: 100%;
  padding: 3rem;
  box-shadow: -3px 0 14px 0 rgba(0, 0, 0, 0.4);
  z-index: 999;
  transition: var(--transition);
}
.navbar__close--btn{
  position: absolute;
  right: auto;
  top: 0;
  cursor: pointer;
  transition: var(--transition);
}
.navbar__close--btn:hover{
  opacity: 0.9;
}
.navbar__smallscreen .nav__link{
  transition: var(--transition);
  letter-spacing: 1px;
  font-size: 1.8rem;
}
.navbar__smallscreen .nav__link:hover{
  padding-left: 1rem;
}
.navbar__smallscreen .nav__item{
  margin: 2rem 0;
}

@media(min-width: 578px){
  .navbar__smallscreen{
      width: 300px;
  }
}

@media(min-width: 992px){
  .navbar__open--btn{
      display: none;
  }
  .navbar__collapse{
      display: flex;
  }
  .navbar__collapse .navbar__nav{
      display: flex;
      align-items: center;
  }
  .navbar__collapse .nav__item{
      margin-left: 2rem;
  }
  .navbar__smallscreen{
      display: none;
  }
}